<script>
import firebase from 'firebase'
import { db } from '@/main.js';
  export default {
			created() {
								firebase.auth().onAuthStateChanged(user => {
							if (user) {
								console.log("logged user details",user)
								this.uid = user.uid
								this.phno = user.phoneNumber.slice(3)

								console.log("billing il user id", this.uid)
								console.log("billing il user number", this.name)


								db.collection('users').where("uid" , "==" , this.uid).get().then((querySnapshot) => {
								querySnapshot.forEach((doc) => {
									console.log(doc.id, " => ", doc.data());
									let user_details = doc.data()
									this.Udata = user_details
									this.name = this.Udata.FirstName
									this.phno = this.Udata.phoneNumber
									this.virtualnumber = this.Udata.virtualNumber

									
								})
							})
							.catch((error) => {
								console.log("Error getting documents: ", error);
							})
						}
					})
    },
    data () {
      return {
        name: '',
        phno : '',
				Udata : [],
				virtualnumber : '',
        }
      },
    }

</script>