							console.log(doc.id, " => ", doc.data());
							let user_details = doc.data()
							this.calldetails = user_details
							this.detail = Object.assign({}, this.detail, { callstatus : this.calldetails.callstatus , name: this.calldetails.name ,dateTime : this.calldetails.dateTime , conversationduration : this.calldetails.conversationduration ,callerNumber : this.calldetails.callerNumber })
							this.realdata.push(this.detail)
							// this.userUid = this.Udata.uid
